import React from 'react'


const FaltantesMaquetar = (props) => {
    return (
        <div className="bg-secondary m-1 ancho alto">
                    <h4 className="titulo text-center" >{props.todo.title}</h4>
                    <button className="btn  btn-danger parrafo" >{`complete: ${props.todo.completed = false}`}</button>
                </div>
    )
}



export default FaltantesMaquetar