import React from 'react'
import {
         BrowserRouter as Router,
         Switch,
         Route,
         Link
} from "react-router-dom";
import Completos from './Completos';
import Faltantes from './Faltantes';
import Todos from './Todos';

const container = () => {

    

    return (

        <Router>
        <div className ="container-fluid p-5 mb-2 bg-secondary text-white text-center ">
            <h1>TO DO LIST</h1>
           <div className="mt-5">
           <button className="btn btn-primary m-3">
             <Link to="/Todos" className="text-white">Todos</Link>  
            </button>
            <button className="btn btn-primary m-3">
            <Link to="/completos" className="text-white">completos</Link>  
            </button>
            <button className="btn btn-primary m-3">
            <Link to="/faltantes" className="text-white">faltantes</Link>  
            </button>  
           </div>
        </div>

        <Switch>
          <Route exact path="/todos">
            <Todos/>
          </Route>  

          <Route exact path="/completos">
            <Completos/>
          </Route>  

          <Route exact path="/faltantes">
            <Faltantes/>
          </Route>  

        </Switch>

        
 
        </Router>

    )
}

export default container
