import React from 'react'


const TodosMaquetar = (props) => {
    return (
        <div className="bg-secondary m-1 ancho alto">
                    <h4 className="titulo text-center" >{props.todo.title}</h4>
                    <button className="btn  btn-primary parrafo" >{`complete: ${props.todo.completed}`}</button>
                </div>
    )
}

TodosMaquetar.propTypes = {

}

export default TodosMaquetar
